<script>
import cabinet from './cabinetForm'
import { addMachine,getMachineSelect,getVendingModel,getPositionList,vendingModelList,getCommodity } from '@/api/equipment'
import NProgress from 'nprogress'

export default {
  data() {
        return {
          // 厂家、货柜类型、网络类型、售卖状态查询接口
          cocmodityList:[],//商品列表
          hdList:{},//cols,
          isProduct:1,//临时使用
          isProhibit:-1,//临时使用
          isIndeterminate: true,
          vendingModeData:{},//货到行和列的数据
     
          checkedCities: [],

          checkAll: false,
          disabled:true,
          input10:'',
          isD:2,//临时使用
          PlistVisible:false,
          innerVisible:false,
          editableTabsValue: '1',
          editableTabs: [],
          tabIndex: 1,
          ManufactorOptions:[],//厂家列表---下拉
          CabinetOptions:[],//货柜类型---下拉
          stateOptions:[],//售卖状态--下拉
          networkOptions:[],//网络--下拉
          modelOptions:[],//机型希腊
          options:[],
          palychek:[],
          restaurants:[],//点位
          companyName: '',
          value:"",
          input:'',
          formLabelWidth: '120px',
          addForm1:{},
          addForm: {
            siteCode:"",  //售货机编码
            siteName:"",  // 售货机名称
            factoryId:"", // 厂家编号
            cabinetType:"", // 货柜类型 01:商店机 02:弹簧机 03:格子机
            deviceId:"",  // 机型编码
            pointId:"", // pointId
            payType:"", //支付类型
            mediaType:1, // 播放视频 1:支持 2:无
            netWork:1,  // 网络制式 1:GPRS  2:网口
            sellState:1,  // 售卖状态 1:可售卖 2:不可售卖
            description:"", // 描述
            lanes:[],//主柜子货到
            cabinets:[  // 副柜
              
            ]
          },
          cabinetsObj: {
                hangType:"", //外挂类型 1:是cabinetsObj 2:否
                pointCode:"", // 串口号
                factoryId:"", // 厂家编号
                cabinetType:"", // 货柜类型 01:商店机 02:弹簧机 03:格子机
                deviceId:"", // 机型编码
                lanes:[ // 货道
                {
                  laneId:1, // 货道编号(从左到右、从上到下)
                  row:1, // 行数
                  col:1,  // 列数
                  arrange:1,  // 排数
                  lanep: {  // 货道商品
                    laneSId:1,  // 货道开始编号(一个商品占多个货道)
                    laneEId:2, // 货道结束编号(如果只有一个商品则只有开始货道编号)
                    productId:"", //商品编号
                    productPic:"",  // 商品图片
                    salePrice:"", // 售卖价格
                    capacity:12,  // 存放容量
                    warnCap:3,  // 缺货阈值
                    laneSate:"1"  // 货道状态 1:正常 2:停用
                  }
                }
                ]
          },
          lanes:[],
          Rules:{
              siteCode:[
                { required: true, message: ' ', trigger: 'change' }
              ],
              siteName:[
                { required: true, message: ' ', trigger: 'change' }
              ],
              dictLabel:[
                { required: true, message: ' ', trigger: 'change' },
               
              ],
              factoryId:[
                { required: true, message: ' ', trigger: 'change' },
              ],
              cabinetType:[
                 { required: true, message: ' ', trigger: 'change' },
              ],
              netWork:[
                 { required: true, message: ' ', trigger: 'change' },
              ],
              sellState:[
                 { required: true, message: ' ', trigger: 'change' },
              ],
              deviceId:[
                 { required: true, message: ' ', trigger: 'change' },
              ],
              pointId:[
                { required: true, message: ' ', trigger: 'change' },
              ]
          },
          hd_id:'',
          hd_dx:[],//单选临时存放
          select_hd_list:[],//最终拼接的货道数据
          hd_all:[],
         
        }
  },
  watch: {
   
  },
  methods: {
    enableUp(){//启用
        var _this = this;
        this.vendingModeData.rows.forEach(function(row){
            for(var i =0; i<row.cols.length; i++){
              for(var j=0; j<_this.checkedCities.length; j++){
                    if(_this.checkedCities[j].laneId==row.cols[i].laneId){
                        row.cols[i].lanep.laneSate= "1"
                        _this.checkedCities[j].lanep.laneSate = "1"
                        _this.checkedCities.splice(j--,1)
                    }
              }
            }
        })
    },
    disableUp(){//禁用
        var _this = this;
        this.vendingModeData.rows.forEach(function(row){
            for(var i =0; i<row.cols.length; i++){
              for(var j=0; j<_this.checkedCities.length; j++){
                    if(_this.checkedCities[j].laneId==row.cols[i].laneId){
                        row.cols[i].lanep.laneSate = "2"
                        _this.checkedCities[j].lanep.laneSate = "2"
                    }
              }
            }
        })
    },
    add_hd(row){
      this.hd_id = row.laneId
      this.PlistVisible = true
    },
    selectGoods(data){//选择货到商品
      var _this =this;
      _this.vendingModeData.rows.forEach(function(row,index){//便利所有货到找到选择的货到并赋值
          for(var i = 0; i<row.cols.length; i++){
              if(_this.hd_id == row.cols[i].laneId){
                  row.cols[i].lanep.title= data.fullName
                  row.cols[i].lanep.laneSate = 3
                  row.cols[i].lanep.salePrice= data.salePrice
                  row.cols[i].lanep.productPic= data.productPic
                  //_this.select_hd_list.push(row.cols[i])
                  _this.checkedCities.push(row.cols[i])

              }
          }
      })
      console.log("最终数据：：",_this.checkedCities)
      this.PlistVisible = false;
     
    },
    del_hd(data){//删除本地货道商品
      var _this =this;
      _this.vendingModeData.rows.forEach(function(row,index){//便利所有货到找到选择的货到并赋值
          for(var i = 0; i<row.cols.length; i++){
              if(data.laneId == row.cols[i].laneId){
                  row.cols[i].lanep.laneSate = 1
              }
          }
      })
      for (var i = 0; i< _this.checkedCities.length; i++) {
            if(data.laneId ==_this.checkedCities[i].laneId ){
                _this.checkedCities.splice(i--,1)
            }
      }
      console.log(_this.checkedCities)
      this.isProduct = 1;
    },
    hd_cancel(){
        var _this = this;
        this.$confirm('确认要放弃配置吗？', '提示', {}).then(() => {
          _this.innerVisible = false
          _this.vendingModeData.rows.forEach(function(row,index){
            for(var i = 0; i<row.cols.length; i++){
                    row.cols[i].lanep.laneSate = 1
            }
          })
          _this.checkedCities = []
        })
    },
    //新增tab
    addTab(targetName) {
      localStorage.setItem("cabinetsData"+targetName,JSON.stringify(this.cabinetsObj))
      //this.addForm.cabinets.push()
        let _this =this;
        let newTabName = ++this.tabIndex + '';
        this.addForm.cabinets.push({
          title: '挂载柜'+newTabName,
          name: newTabName,
          addform:"addForm"+newTabName,
          //副柜信息
          hangType:"1", //外挂类型 1:是cabinetsObj 2:否
          pointCode:"", // 串口号
          factoryId:"", // 厂家编号
          cabinetType:"", // 货柜类型 01:商店机 02:弹簧机 03:格子机
          deviceId:"码", // 机型编码
          lanes:[ // 货道
          {
            laneId:1, // 货道编号(从左到右、从上到下)
            row:1, // 行数
            col:1,  // 列数
            arrange:1,  // 排数
            lanep: {  // 货道商品
              laneSId:1,  // 货道开始编号(一个商品占多个货道)
              laneEId:2, // 货道结束编号(如果只有一个商品则只有开始货道编号)
              productId:"", //商品编号
              productPic:"",  // 商品图片
              salePrice:"", // 售卖价格
              capacity:12,  // 存放容量
              warnCap:3,  // 缺货阈值
              laneSate:"1"  // 货道状态 1:正常 2:停用
            }
          }
          ]




        });
        this.editableTabsValue = newTabName;
    },
    //减少tab
    removeTab(targetName) {
        localStorage.removeItem("cabinetsData"+targetName)

        let tabs = this.editableTabs;
        let activeName = this.editableTabsValue;
        if (activeName === targetName) {
          tabs.forEach((tab, index) => {
            if (tab.name === targetName) {
              let nextTab = tabs[index + 1] || tabs[index - 1];
              if (nextTab) {
                activeName = nextTab.name;
              }
            }
          });
        }
        
        this.editableTabsValue = activeName;
        this.editableTabs = tabs.filter(tab => tab.name !== targetName);
    },
    handleCheckedCitiesChange(value) {
        this.addForm.payType = value.join(',')
    },
    addSubmitForm(formName) {//提交新增窗口
        this.$refs[formName].validate((valid) => {
          if (valid) {
          this.$confirm('确认提交吗？', '提示', {}).then(() => {
               NProgress.start();
                      let para = Object.assign({}, this.addForm);
                      para.lanes = this.checkedCities
                      addMachine(para).then((res) => {
                            if(res.zhead.reTCode=="0000"){
                            NProgress.done();
                            this.$notify({
                              title: '成功',
                              message: res.zhead.retMsg,
                              type: 'success'
                            });
                            this.$refs['addForm'].resetFields();
                            this.$router.push(
                                  {path:'/equipment/machine/index'}
                            );
                            }else{
                              this.$notify({
                                title: '失败',
                                message: res.zhead.retMsg,
                                type: 'error'
                              });
                          }
                      });
          });
            //this.dialogAddTableVisible = false
          } else {
            console.log('error submit!!');
            return false;
          }
        });
    },
   
    resetForm(formName) {//重置表单
        this.$refs[formName].resetFields();
    },

    getMachineSelectList(type) {
      var _this = this;
      const listQuery = {
        dictType:type,
      }
      getMachineSelect(listQuery).then(response => {
         if(response.zhead.reTCode==="0000"){
         
          NProgress.done();
          /*
          字典类型,厂商：sys_factory；货柜类型sys_cabinet_type；网络类型：sys_net_work；售卖状态：sys_sell_state
          */ 

          if(type==="sys_factory")
          {
            _this.ManufactorOptions= response.zbody.datas.rows;//厂家列表---下拉
             console.log("厂家列表：：",_this.ManufactorOptions)
          }
          else if(type==="sys_cabinet_type")
          {
             _this.CabinetOptions= response.zbody.datas.rows;//货柜类型---下拉
             console.log("货柜类型：：",_this.CabinetOptions)
          }
          else if(type==="sys_net_work")
          {
              _this.networkOptions= response.zbody.datas.rows;//网络类型---下拉
              console.log("网络类型：：",_this.networkOptions)
          }
          else if(type==="sys_sell_state")
          {
             _this.stateOptions= response.zbody.datas.rows;//售卖状态--下拉
             console.log("售卖状态：：",_this.stateOptions)
          }
         }else{
             this.$notify({
              title: '失败',
              message: res.zhead.retMsg,
              type: 'error'
            });
         }
      })
    },
    //机型查询接口
    getModel(){
      var _this = this;
      const listQuery = {
      }
      getVendingModel(listQuery).then(response => {
           if(response.zhead.reTCode==="0000"){
            _this.modelOptions=response.zbody.datas.rows
            console.log("机型：：",response.zbody.datas.rows)
            NProgress.done();
           }else{
               this.$notify({
                title: '失败',
                message: res.zhead.retMsg,
                type: 'error'
              });
           }
        })
    },
    changeDevice(){
      this.disabled = false
    },
    //获取一个机型里的货道查询接口
    getvendingModel(row){
      debugger
      var _this = this;
      this.innerVisible = true
      const listQuery = {
        logid:_this.addForm.deviceId
      }
      vendingModelList(listQuery).then(response => {
           if(response.zhead.reTCode==="0000"){
            //_this.modelOptions=response.zbody.datas.rows
            _this.vendingModeData = response.zbody.datas


            _this.vendingModeData.rows.forEach(function(row,index){
              for(var i = 0; i<row.cols.length; i++){
                 _this.hd_all.push(row.cols[i])
              }

            })
            console.log("全部货到",_this.hd_all)




            console.log("一个机型里的货道：：",response.zbody.datas)
            NProgress.done();
           }else{
               this.$notify({
                title: '失败',
                message: res.zhead.retMsg,
                type: 'error'
              });
           }
        })
    },
    querySearchAsync(queryString, cb) {//输入搜索点位
        var restaurants = this.restaurants;
        var results = queryString ? restaurants.filter(this.createStateFilter(queryString)) : restaurants;
        var _this = this;
        const listQuery = {
          name:_this.companyName
        }
        getPositionList(listQuery).then(response => {
          
          _this.restaurants = response.zbody.datas.rows
          console.log(_this.restaurants)
          cb(results);
        })
    },
    createStateFilter(queryString) {
        return (state) => {
          return (state.name.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
        };
    },
    handleSelect(item) {
        this.addForm.pointId = item.pointId
        this.addForm.pointName = item.name
        console.log(item.pointId);
    },
      // 货到
    handleCheckAllChange(val) {//全选
      this.checkedCities = val ? this.vendingModeData.rows : [];
      this.isIndeterminate = false;
    },
    handleCheckedCitiesChange_hd(value) {//单选
      let checkedCount = value.length;
      this.checkAll = checkedCount === this.vendingModeData.rows.length;
      this.isIndeterminate = checkedCount > 0 && checkedCount < this.vendingModeData.rows.length;


    },
    //查询商品列表
    getCommodityList(){
      var _this = this;
      const listQuery = {
        typeId:'',
        name:'',
        productCode:''
      }
      getCommodity(listQuery).then(response => {
           if(response.zhead.reTCode==="0000"){
            _this.cocmodityList=response.zbody.datas.rows
            console.log("商品列表：：",response.zbody.datas.rows)
            NProgress.done();
           }else{
               this.$notify({
                title: '失败',
                message: res.zhead.retMsg,
                type: 'error'
              });
           }
        })
    },
  },

  mounted () {
        this.getMachineSelectList("sys_factory")
        this.getMachineSelectList("sys_cabinet_type")
        this.getMachineSelectList("sys_net_work")
        this.getMachineSelectList("sys_sell_state")
        this.getModel()
        this.getCommodityList()
  },
  components: {
    cabinet
  }
}
